<template>
  <div class="comment-post">
    <van-field
      class="post-field"
      v-model="message"
      rows="2"
      autosize
      type="textarea"
      maxlength="50"
      placeholder=""
      show-word-limit
    />
    <van-button
      class="post-btn"
      @click="commentSubmit"
      :disabled="!message.trim().length"
      ></van-button
    >
  </div>
</template>

<script>
import { setComment } from '@/api/comment.js'
export default {
  name: 'CommentPost',
  components: {},
  props: {
    artId: {
      type: [Object, String, Number],
      required: true
    },
    artIdfromReply: {
      type: [Object, String, Number]

    }
  },
  data () {
    return {
      message: ''
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    close () {
      this.$emit('closeCommentPop')
    },
    async commentSubmit () {
      // target integer  ididid
      // content string  
      // art_id integer  idid
      try {
        const { data: res } = await setComment({
          target: this.artId,
          content: this.message,
          art_id: this.artIdfromReply || null

        })

        this.$toast.success('')
        this.message = ''
        if (res) {
          this.$emit('addOneComment', res.data)
        }
      } catch (e) {
        if (e.response.status === 401) {
          this.$toast.fail('')
        } else {
          this.$toast.fail('')
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
.comment-post {
  display: flex;
  align-items: center;
  padding: 32px 0 32px 32px;
  .post-field {
    background-color: #f5f7f9;
  }
  .post-btn {
    width: 150px;
    border: none;
    padding: 0;
    color: #6ba3d8;
    &::before {
      display: none;
    }
  }
}
</style>
