<template>
  <van-button
    :class="iscollect ? 'active' : ''"
    :icon="iscollect ? 'star' : 'star-o'"
    @click="collectToggle"
    :loading="loading"
  />
</template>

<script>
import { goCollect, cancelCollect } from '../../api/user'
export default {
  data () {
    return {
      loading: false
    }
  },
  model: {
    prop: 'iscollect',
    event: 'change'
  },
  props: {
    iscollect: {
      type: Boolean,
      required: true

    },
    followed_id: {
      type: [String, Number, Object],
      required: true
    }
  },
  methods: {
    async collectToggle () {
      try {
        this.loading = true
        if (this.iscollect) {
          await cancelCollect(this.followed_id)
          this.$toast.success('!')
          this.$emit('change', !this.iscollect)
        } else {
          await goCollect(this.followed_id)
          this.$toast.success('!')
          this.$emit('change', !this.iscollect)
        }
      } catch (e) {
        if (e.response.status === 401) {
          this.$toast.fail('')
        } else { this.$toast.fail('') }
      } finally {
        this.loading = false
      }
    }
  }

}
</script>

<style lang="scss" scoped>
.active {
  color: orange !important;
}
.van-button--default {
  border: 0;
}
</style>
