<template>
  <van-list
    :immediate-check="false"
    v-model="loading"
    :finished="finished"
    finished-text=""
    @load="onLoad"
    :error.sync="error"
    error-text=""
  >
    <my-comment
      @changeLiking="changeLiking($event, item)"
      v-for="(item, index) in list"
      :key="'a' + index"
      :comment="item"
    />
  </van-list>
</template>

<script>
import { getComments } from '@/api/comment.js'
import MyComment from './comment'
export default {
  name: 'comment-list',
  components: { MyComment },
  props: {
    // newOneCommentCommoentPost
    newOneComment: { type: Object },
    artId: {
      type: [String, Number, Object],
      required: true
    },
    type: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      queryInfo: {
        type: this.type,
        source: this.artId.toString(),
        offset: null,
        limit: 10
      },
      list: [],
      error: false,
      loading: false,
      finished: false
    }
  },
  created () { this.onLoad() },
  watch: {

    newOneComment: function (val) {
      if (val) {
        // 
        this.list.unshift(val)
      }
    }
  },
  methods: {
    // 
    changeLiking (event, item) {
      item.is_liking = event[0]
      item.like_count = event[1]
    },
    async onLoad () {
      // 
      // setTimeout  ajax 
      try {
        const { data: res } = await getComments(this.queryInfo)
        // 
        // console.log(res.data.results.length)
        if (res.data.results.length === 0) {
          this.finished = true
        }
        // console.log(res)
        // console.log(res.data.results)
        // 
        this.$emit('setTotalCount', res.data.total_count)
        this.list.push(...res.data.results)
        // console.log(this.list)
        this.queryInfo.offset = res.data.last_id
      } catch (e) {
        this.error = true
      }
      // 
      this.loading = false
    }
  }
}
</script>

