<template>
  <van-button
    round
    size="small"
    icon="plus"
    type="info"
    v-if="!is_followed"
    @click="followToggel"
    :loading="loading"
    ></van-button
  >
  <van-button
    round
    size="small"
    :loading="loading"
    type="default"
    v-else
    @click="followToggel"
    ></van-button
  >
</template>

<script>
import { goFollow, cancelFollow } from '../../api/user'
export default {
  props: ['is_followed', 'followed_id'],
  data () {
    return {
      loading: false
    }
  },
  methods: {
    async followToggel () {
      console.log(this.followed_id)
      if (this.is_followed) {
        try {
          this.loading = true
          await cancelFollow(this.followed_id)
          this.$emit('setIsFollowed', !this.is_followed)
        } catch (e) {
          this.$toast.fail('')
        }
      } else {
        try {
          this.loading = true
          await goFollow(this.followed_id)
          this.$emit('setIsFollowed', !this.is_followed)
        } catch (e) {
          if (e.response.status === 401) {
            this.$toast.fail('')
          } else
          if (e.response.status === 400) { this.$toast.fail('') } else { this.$toast.fail('') }
        }
      }
      this.loading = false
    }
  }

}
</script>

